@charset "UTF-8";

.slick-prev, .slick-next {
  font-size: 0;
  line-height: 0;
  position: absolute;
  top: 50%;
  display: block;
  width: 20px;
  height: 20px;
  margin-top: -10px;
  padding: 0;
  cursor: pointer;
  color: transparent;
  border: none;
  outline: none;
  background: transparent; }

.slick-prev {
  &:hover, &:focus {
    color: transparent;
    outline: none; } }

.slick-next {
  &:hover, &:focus {
    color: transparent;
    outline: none; } }

.slick-prev {
  &:hover:before, &:focus:before {
    opacity: 1; } }

.slick-next {
  &:hover:before, &:focus:before {
    opacity: 1; } }

.slick-prev.slick-disabled:before, .slick-next.slick-disabled:before {
  opacity: .25; }

.slick-prev:before, .slick-next:before {
  font-family: 'slick';
  font-size: 20px;
  line-height: 1;
  opacity: .75;
  color: white;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

.slick-prev {
  left: 25px;
  z-index: 1; }

[dir='rtl'] .slick-prev {
  right: 25px;
  left: auto; }

.slick-prev:before {
  content: '<'; }

[dir='rtl'] .slick-prev:before {
  content: '>'; }

.slick-next {
  right: 25px; }

[dir='rtl'] .slick-next {
  right: auto;
  left: 25px; }

.slick-next:before {
  content: '>'; }

[dir='rtl'] .slick-next:before {
  content: '<'; }

.slick-dots {
  position: absolute;
  bottom: -45px;
  display: block;
  width: 100%;
  padding: 0;
  list-style: none;
  text-align: center;
  li {
    position: relative;
    display: inline-block;
    width: 20px;
    height: 20px;
    margin: 0 5px;
    padding: 0;
    cursor: pointer;
    button {
      font-size: 0;
      line-height: 0;
      display: block;
      width: 20px;
      height: 20px;
      padding: 5px;
      cursor: pointer;
      color: transparent;
      border: 0;
      outline: none;
      background: transparent;
      &:hover, &:focus {
        outline: none; }
      &:hover:before, &:focus:before {
        opacity: 1; }
      &:before {
        font-family: 'slick';
        font-size: 6px;
        line-height: 20px;
        position: absolute;
        top: 0;
        left: 0;
        width: 20px;
        height: 20px;
        content: '•';
        text-align: center;
        opacity: .25;
        color: black;
        -webkit-font-smoothing: antialiased;
        -moz-osx-font-smoothing: grayscale; } }
    &.slick-active button:before {
      opacity: .75;
      color: black; } } }
