@import "bourbon";
@import "vars";
@import "libs";

.main-cat_items {
	margin-top: 30px; }

.main-cat_item {
	display: inline-block;
	vertical-align: top;
	width: 200px;
	height: 190px;
	position: relative;
	margin-right: 10px;
	background-size: contain;
	background-position: center;
	background-repeat: no-repeat;
	&:last-child {
		margin-right: 0; }
	&:after {
		position: absolute;
		content: '';
		display: block;
		width: 22px;
		height: 22px;
		background: url('/img/main-cat_item-arrow.png') no-repeat;
		bottom: 20px;
		left: 15px; }
	&:hover {
		&:before {
			position: absolute;
			content: '';
			display: block;
			width: 100%;
			height: 100%;
			background: rgba(224, 32, 0, 0.7);
			top: 0;
			left: 0; }
		&:after {
			background-position: -22px 0; }
		.main-cat_item-title {
			color: #fff; } } }

.main-cat_item-title {
	text-transform: uppercase;
	text-align: left;
	position: absolute;
	z-index: 3;
	left: 15px;
	top: 25px;
	line-height: 20px;
	color: #000; }

.action_items-block {
	margin-top: 35px; }

.action_items-title {
	font-family: $bold-font;
	color: $green;
	font-size: em(24);
	position: relative;
	&:after {
		position: absolute;
		content: '';
		display: block;
		width: 8px;
		height: 13px;
		background: url('/img/title-arrow.png') no-repeat;
		right: -15px;
		top: 12px; }
	&.related {
		&:after {
			display: none; } } }

.product_items-block {
	margin-top: 30px;
	text-align: center; }

.product_item {
	display: inline-block;
	vertical-align: top;
	width: 200px;
	height: 340px;
	position: relative;
	margin-right: 10px;
	border: 2px solid #e8e8e8;
	padding: 12px 6px;
	color: #000;
	//&.product_card
	//	margin-right: 30px
	&:focus {
		color: #000;
		text-decoration: none; }
	&:hover {
		color: #000;
		text-decoration: none; }
	&:last-child {
		margin-right: 0; } }

.product_item-pic_wrap {
	display: table;
	margin: 0 auto;
	text-align: center;
	position: relative; }

.product_item-sale {
	position: absolute;
	display: none;
	right: -20px;
	top: 5px;
	color: $red;
	font-family: $bold-font;
	&:after {
		position: absolute;
		display: block;
		content: '';
		width: 40px;
		height: 40px;
		transform: rotate(-45deg);
		border: 2px solid $red;
		top: -6px;
		right: -1px; } }

.product_item-pic_container {
	display: table-cell;
	height: 120px;
	width: inherit;
	vertical-align: middle;
	margin: 0 auto;
	text-align: center; }

.product_item-pic {
	margin: 0 auto;
	max-width: 180px;
	max-height: 120px;
	display: inline;
	object-fit: contain; }

.product_item-title {
	font-family: $bold-font;
	display: block;
	text-align: center;
	height: 50px;
	line-height: 22px;
	overflow: hidden;
	color: #000;
	text-decoration: none;
	&:hover {
		text-decoration: none; } }

.product_item-short-descr {
	font-size: em(12);
	line-height: 16px;
	text-align: center;
	height: 50px;
	overflow: hidden;
	text-overflow: ellipsis;
	white-space: pre-wrap; }

.product_item-price_wrapper {
	display: block;
	text-align: center; }

.product_item-sale_price {
	display: inline-block;
	vertical-align: middle;
	font-family: $bold-font;
	color: #5b5b5b;
	text-decoration: line-through;
	margin-right: 10px; }

.product_item-price {
	display: inline-block;
	vertical-align: middle;
	font-family: $bold-font;
	color: $red; }

.product_item-btn {
	width: 120px;
	height: 36px;
	line-height: 36px;
	text-align: center;
	margin: 10px auto 0;
	background: $red;
	color: #fff;
	text-transform: uppercase; }

.item_review_block {
	margin-top: 10px;
	padding: 0 2px; }

.reviewStars {
	float: left;
	line-height: 16px; }

.reviewStar {
	display: inline-block;
	background: url('/img/stars_rating.png') no-repeat;
	height: 14px;
	width: 14px;
	&.active {
		background-position: 0 -14px; } }

.stars_count {
