	font-size: 12px;
	color: #a4a4a4; }

.item_reviews {
	float: right;
	font-size: em(14);
	text-decoration: underline;
	position: relative;
	top: -3px; }

.trust-block {
	margin-top: 30px; }

.trust-title {
	font-family: $bold-font;
	color: $green;
	font-size: em(24);
	display: block; }

.trust-item {
	display: inline-block;
	vertical-align: top;
	width: 200px;
	height: 150px;
	margin-right: 10px;
	position: relative;
	margin-top: 10px;
	&.payment {
		&:after {
			position: absolute;
			content: '';
			display: block;
			width: 34px;
			height: 38px;
			background: url('/img/payment_icon.png') no-repeat;
			top: 40px;
			left: calc(50% - 21px); }
		&:hover {
			&:after {
				background-position: -35px 0; } } }
	&.delivery {
		&:after {
			position: absolute;
			content: '';
			display: block;
			width: 40px;
			height: 27px;
			background: url('/img/delivery_icon.png') no-repeat;
			top: 45px;
			left: calc(50% - 24px); }
		&:hover {
			&:after {
				background-position: -41px 0; } } }
	&.troubleshooting {
		&:after {
			position: absolute;
			content: '';
			display: block;
			width: 35px;
			height: 39px;
			background: url('/img/troubleshooting_icon.png') no-repeat;
			top: 38px;
			left: calc(50% - 24px); }
		&:hover {
			&:after {
				background-position: -35px 0; } } }
	&.experience {
		&:after {
			position: absolute;
			content: '';
			display: block;
			width: 30px;
			height: 36px;
			background: url('/img/experience_icon.png') no-repeat;
			top: 40px;
			left: calc(50% - 21px); }
		&:hover {
			&:after {
				background-position: -30px 0; } } }
	&:last-child {
		margin-right: 0; }
	&:before {
		position: absolute;
		content: '';
		display: block;
		width: 70px;
		height: 70px;
		border: 2px solid $red;
		top: 25px;
		left: calc(50% - 40px);
		transform: rotate(-45deg);
		transition: 0.3s; }
	&:hover {
		&:before {
			background: $red; } } }

.trust-item_title {
	font-family: $bold-font;
	position: absolute;
	bottom: 0;
	width: 100%;
	text-align: center; }

.seo-block {
