/* catalog */

.breadcrumbs {
	margin-top: 20px; }

.breadcrumbs_item {
	display: inline-block;
	vertical-align: middle;
	position: relative;
	padding-right: 18px;
	&:after {
		position: absolute;
		display: block;
		content: '';
		right: 0;
		top: 8px;
		width: 10px;
		height: 16px;
		background: url(/img/menu_arrow.png) no-repeat;
		background-size: 150%; }
	&:last-child {
		max-width: 100%;
		text-overflow: ellipsis;
		overflow: hidden;
		white-space: nowrap;
		&:after {
			display: none; } } }

.breadcrumbs_item-link {
	color: #000;
	&:hover {
		color: #000; }
	&:focus {
		color: #000; }
	&.active {
		color: $green; } }

.filters {
	font-family: $bold-font;
	.sidebar_subtitle {
		background: $green;
		color: #fff;
		text-transform: uppercase;
		border: 1px solid $green;
		border-bottom: 3px solid #008a39;
		height: 40px;
		padding: 5px 15px;
		&.secondary {
			background: #fff;
			color: #4b4b4b;
			border: none;
			border-bottom: 2px solid #e8e8e8; } } }

.filters_secondary {
	border-right: 2px solid #e8e8e8;
	.showHideContent {
		&:after {
			position: absolute;
			content: '';
			display: block;
			background: url('/img/menu_arrow.png') -20px 0px no-repeat;
			width: 10px;
			height: 16px;
			right: 12px;
			top: 13px;
			transform: rotate(90deg); } } }

.layout-slider {
	margin-bottom: 90px; }

.showHideContent {
	cursor: pointer;
	position: relative;
	display: block;
	margin-bottom: 15px;
	&:after {
		position: absolute;
		content: '';
		display: block;
		background: url('/img/menu_arrow.png') -10px 0px no-repeat;
		width: 10px;
		height: 16px;
		right: 12px;
		top: 13px;
		transform: rotate(90deg); }
	&.show:after {
		top: 11px;
		right: 12px;
		transform: rotate(270deg); }
	&:hover {
		text-decoration: none;
		&:after {
			background: url('/img/menu_arrow.png') -10px 0px no-repeat; }
		.sidebar_subtitle {
			color: #fff;
			background: #4b4b4b;
			border: 1px solid #4b4b4b; } }
	&.recommend {
		&:after {
			display: none; } } }

.recommend {
	text-decoration: none;
	&:hover, &:focus {
		text-decoration: none; } }

.hidden_content {
	margin-bottom: 15px;
	text-align: center; }

.filter {
	input[type="radio"] {
		display: none;
		+ label {
			background: url(/img/check_box.png) 10px -75px no-repeat;
			padding-left: 40px;
			padding-right: 13px;
			display: table-cell;
			vertical-align: middle;
			width: 100%;
			position: relative;
			z-index: 100;
			cursor: pointer;
			outline: none;
			&:hover {
				background-position: 10px -75px; } }
		&:checked + label {
			color: $green;
			background-position: 10px 7px; } } }

.readmore-js-toggle {
	display: inline-block !important;
	width: auto !important;
	color: $green;
	text-align: center;
	text-decoration: underline;
	position: relative;
	display: block;
	&:hover, &:focus {
		color: $green; }
	&.more_filters {
		&:before {
			position: absolute;
			display: block;
			content: '';
			background: url(/img/title-arrow.png) no-repeat;
			width: 20px;
			height: 14px;
			transform: rotate(90deg);
			left: -21px;
			top: 13px; } }
	&.less_filters {
		margin-top: 10px;
		&:before {
			position: absolute;
			display: block;
			content: '';
			background: url(/img/title-arrow.png) no-repeat;
			width: 20px;
			height: 14px;
			transform: rotate(-90deg);
			left: -21px; } } }


.cat_menu-list {
	width: 100%;
	position: relative;
	text-align: left;
	.cat_menu-item {}
	&:first-child {
		margin-top: -15px; } }

.cat_menu-item {
	border: 1px solid #4b4b4b;
	border-top: none;
	&.brand {
		text-transform: uppercase; }
	&.news {
		text-transform: uppercase;
		&:after {
			display: none; } }
	&.filter {
